import React, { useCallback, useEffect, useState } from 'react'
import * as logosmall from '../../../assets/Clean Walk/Loading_Screen/Search.logo.small.jpg.png'
import * as midlogo from '../../../assets/images/Loading_Screen/LD_CSF_Mid_logo.png'
import * as DULTlogo from '../../../assets/images/Loading_Screen/LD_DULT_Logo.png'
import * as BSFlogo from '../../../assets/images/Loading_Screen/LD_BSF_logo.png'
import * as divideLine from '../../../assets/images/Loading_Screen/LD_divideLine.png'
import * as SGlogo from '../../../assets/images/Loading_Screen/LD_SG_Logo.png'
import * as bgBelow from '../../../assets/Clean Walk/Levels_Screen/BG_bottom.png'
import * as XarpieLabs from '../../../assets/Clean Walk/Loading_Screen/Xarpie Labs.png'
import * as mediaCannibal from '../../../assets/Clean Walk/Loading_Screen/media.cannibal.png'
import './style.css'
import { useHistory } from 'react-router-dom'
import Spinner from 'components/Common/Spinner'
import { loader, SyncData } from 'components/Common/core'
import IntroText from 'components/Common/IntroText'

import { logostore } from '../../../store/logoStore';
import { logopositionstore } from '../../../store/logopositionStore';

const RenderLoadingScreen = () => {

  const { logo, loadlogo } = logostore();
  const { logoposition, loadlogo_arrayposition } = logopositionstore();
  const history = useHistory();
  const [spinner, setspinner] = useState(true)
  const [introslide, setintroslide] = useState(true)
  const [logoimage, setlogoimage] = useState({})

  const firstTimeloader = () => {
    setTimeout(
      () => {
        history.replace('/StartScreen')
      }, 8000
    );
  }

  useEffect(() => {
    if (!logo) {
      loadlogo()
    }

    if (!logoposition) {
      loadlogo_arrayposition()
    }
    console.log("logo logo :", logoposition);
  }, [])

  const logoimage_position = ((obj: any) => {
    // console.log("array-list-1 :", obj.length, obj);
    const selectcase = (number: any) => {

      if (number === 1)
        return "logostyle1";
      else
        if (number > 1 && number < 4)
          return "logostyle2";
        else
          if (number >= 4 && number <= 6)
            return "logostyle3";
          else
            if (number > 6)
              return "logostyle4";
    }
    let a = selectcase(obj.length)
    return obj.map((obj1: any) => {
      return <img className={a} src={obj1.i_logo} />
    })
  })

  return (
    <>

      <div onClick={() => {
        if (localStorage.getItem('isFirsttime') === null) {
          loader()
          history.replace('/StartScreen')
        } else {
          history.replace('/TaskListScreen')
        }
      }}>
        <div className='loadbgwrapper'>

          <div className="row1">
            <div className="loadinnerrow">
              {(logoposition) && logoposition.section1.map(logoimage_position)}
            </div>
          </div>

          <div className='row2'>
            <div className="innerrowtitle"><u>CONTENT</u></div>
            <div className="loadinnerrow">
              {(logoposition) && logoposition.section2.map(logoimage_position)}
            </div>
          </div>

          <div className='row3'>
            <div className="innerrowtitle"><u>GAME APP DEVELOPMENT</u></div>
            <div className="loadinnerrow">
              {(logoposition) && logoposition.section3.map(logoimage_position)}
            </div>
          </div>

          <div className='row4'>
            <img className='loadbgBelow' src={logoimage.position === 10 && logoimage.i_logo} />
          </div>

        </div>
      </div>

    </>
  )
}

export default RenderLoadingScreen;
