  const history = useHistory()
  const [dashboard, setdashboard] = useState('')
  const [menu_open, setMenu_open] = useState(true)

  const [editdata, seteditdata] = useState({});

  const [instanceid, setinstanceid] = useState('');
  const [levelid, setlevelid] = useState('');


  //instance
  const [instance_listItems, setinstance_listItems] = useState([
    {
      "id": "",
      "i_icon": "",
      "mascot": "",
      "i_title": "",
      "i_stitle": "",
      "i_body": "",
      "i_bodytype": "",
      "i_bodyoptions": "",
      "locations": "",
      "i_media": "",
      "i_misc": "",
    }
  ])

  const [instance_popup, setinstance_popup] = useState(false)
  const [instance_popup1, setinstance_popup1] = useState(false)

  const [i_id, seti_id] = useState('');
  const [i_icon, seti_icon] = useState('');
  const [mascot, setmascot] = useState('');
  const [i_title, seti_title] = useState('');
  const [i_stitle, seti_stitle] = useState('');
  const [i_body, seti_body] = useState('');
  const [i_bodytype, seti_bodytype] = useState({});
  const [i_bodyoptions, seti_bodyoptions] = useState('');
  const [locations, setlocations] = useState('');
  const [i_media, seti_media] = useState('');
  const [i_misc, seti_misc] = useState('');

  const [selectradio, setselectradio] = useState({})
